import _ from 'lodash';
import { toast } from 'react-toastify';
import { utils, Contract, BigNumber } from 'ethers';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../config/tokenBep20Config';
import { BEP20_PROXY } from '../config/web3Config';
import swapHelper from '../helper/swapHelper';
import { DApp, ITokenDetail } from '../model/tokenModel';
import { IRouteSwap, IPriceImpact } from '../model/swapModel';
import { bscProvider, getBep20Contract } from './web3Service';
import { getTokenPool } from '../service/tokenPoolService';
import { getTokenPrice } from '../service/tokenPriceService';

const stableCoinAdress = [
  BUSD_BEP20.address,
  USDT_BEP20.address,
  USDC_BEP20.address,
];
const rootAddress: string[] = [...stableCoinAdress, BNB_BEP20.address];

export async function getTokenPoolSwaps(
  tokens: [ITokenDetail, ITokenDetail]
): Promise<[ITokenDetail, ITokenDetail]> {
  let nonRequestTokens: ITokenDetail[] = [];
  let requestTokens: ITokenDetail[] = [];
  let tokenSwaps: [ITokenDetail, ITokenDetail] = tokens;

  tokens.forEach((token: ITokenDetail, i: number) => {
    if (_.includes(stableCoinAdress, token.address)) {
      nonRequestTokens.push({ ...token, price: 1 });
    }
  });

  requestTokens = tokens.filter((token: ITokenDetail) => {
    return !_.find(nonRequestTokens, { address: token.address });
  });

  if (!!_.size(requestTokens)) {
    requestTokens = await getTokenPool(requestTokens);
  }

  tokens.forEach((token: ITokenDetail, i: number) => {
    tokenSwaps[i] = (_.find(nonRequestTokens, { address: token.address }) ||
      _.find(requestTokens, { address: token.address })) as ITokenDetail;
  });

  return tokenSwaps;
}

export async function getTokenSwaps(
  tokens: [ITokenDetail, ITokenDetail],
  dApp: DApp
): Promise<[ITokenDetail, ITokenDetail]> {
  let nonRequestTokens: ITokenDetail[] = [];
  let requestTokens: ITokenDetail[] = [];
  let tokenSwaps: [ITokenDetail, ITokenDetail] = tokens;

  tokens.forEach((token: ITokenDetail, i: number) => {
    if (_.includes(stableCoinAdress, token.address)) {
      nonRequestTokens.push({ ...token, price: 1 });
    } else if (_.isNil(token.targetPool)) {
      nonRequestTokens.push(token);
    }
  });

  requestTokens = tokens.filter((token: ITokenDetail) => {
    return !_.find(nonRequestTokens, { address: token.address });
  });

  if (!!_.size(requestTokens)) {
    requestTokens = await getTokenPrice(requestTokens, dApp);
  }

  tokens.forEach((token: ITokenDetail, i: number) => {
    tokenSwaps[i] = (_.find(nonRequestTokens, { address: token.address }) ||
      _.find(requestTokens, { address: token.address })) as ITokenDetail;
  });

  return tokenSwaps;
}

export function getRouteSwap(
  tokenInput: ITokenDetail,
  tokenOutput: ITokenDetail,
  dApp: DApp
): IRouteSwap[] {
  // Pancake: BUSD/USDT, BUSD/USDC, BUSD/BNB, USDT/USDC, USDT/BNB
  // Ape: BUSD/USDT, BUSD/USDC, BUSD/BNB

  if (tokenInput.address === tokenOutput.address) {
    return [];
  }

  const routeInput: IRouteSwap = {
    symbol: tokenInput.symbol as string,
    address: tokenInput.address,
  };
  const routeInputPair: IRouteSwap = {
    symbol: tokenInput.targetPool?.quoteSymbol as string,
    address: tokenInput.targetPool?.quoteAddress as string,
  };
  const routeOutput: IRouteSwap = {
    symbol: tokenOutput.symbol as string,
    address: tokenOutput.address,
  };
  const routeOutputPair: IRouteSwap = {
    symbol: tokenOutput.targetPool?.quoteSymbol as string,
    address: tokenOutput.targetPool?.quoteAddress as string,
  };
  let routeSwaps: IRouteSwap[] = [routeInput];
  let routePrev: IRouteSwap;

  if (!rootAddress.includes(routeInput.address)) {
    routeSwaps.push(routeInputPair);
  }

  switch (dApp) {
    case DApp.PANCAKE:
      const unBridge: [string, string] = [
        USDC_BEP20.address,
        BNB_BEP20.address,
      ];

      routePrev = _.last(routeSwaps) as IRouteSwap;

      if (rootAddress.includes(routeOutput.address)) {
        if (routeOutput.address !== routePrev.address) {
          if (
            unBridge.includes(routeOutput.address) &&
            unBridge.includes(routePrev.address)
          ) {
            routeSwaps.push({
              symbol: BUSD_BEP20.symbol as string,
              address: BUSD_BEP20.address,
            });
          }
          routeSwaps.push(routeOutput);
        }
      } else {
        if (routeOutputPair.address !== routePrev.address) {
          if (
            unBridge.includes(routeOutputPair.address) &&
            unBridge.includes(routePrev.address)
          ) {
            routeSwaps.push({
              symbol: BUSD_BEP20.symbol as string,
              address: BUSD_BEP20.address,
            });
          }
          routeSwaps.push(routeOutputPair);
        }

        routeSwaps.push(routeOutput);
      }

      break;

    case DApp.APE:
      routePrev = _.last(routeSwaps) as IRouteSwap;

      if (rootAddress.includes(routeOutput.address)) {
        if (routeOutput.address !== routePrev.address) {
          if (
            routeOutput.address !== BUSD_BEP20.address &&
            routePrev.address !== BUSD_BEP20.address
          ) {
            routeSwaps.push({
              symbol: BUSD_BEP20.symbol as string,
              address: BUSD_BEP20.address,
            });
          }

          routeSwaps.push(routeOutput);
        }
      } else {
        if (routeOutputPair.address !== routePrev.address) {
          if (
            routeOutputPair.address !== BUSD_BEP20.address &&
            routePrev.address !== BUSD_BEP20.address
          ) {
            routeSwaps.push({
              symbol: BUSD_BEP20.symbol as string,
              address: BUSD_BEP20.address,
            });
          }
          routeSwaps.push(routeOutputPair);
        }

        routeSwaps.push(routeOutput);
      }

      break;
  }

  return routeSwaps;
}

export function getPriceImpact(
  amountTraded: number,
  tokenInput: ITokenDetail,
  tokenOutput: ITokenDetail,
  routeSwaps: IRouteSwap[],
  bnbPrice: number
): IPriceImpact {
  if (!swapHelper.validRouteSwaps(routeSwaps)) {
    return {
      priceImpact: -1,
    };
  }

  if (!amountTraded || routeSwaps.length === 0) {
    return {
      priceImpact: 0,
    };
  }

  const fee = 0.0025;
  const routes: IRouteSwap[] = routeSwaps.map(
    (route: IRouteSwap): IRouteSwap => ({
      ...route,
      address: route.address,
    })
  );
  const amountForFee: number = amountTraded * (routes.length - 1) * fee;
  let routeTraded = {
    address: tokenInput.address,
    amount: amountTraded,
    price: tokenInput.price as number,
  };
  let priceImpact: number;

  routes.forEach((route: IRouteSwap, index: number) => {
    if (index === routes.length - 1) {
      return;
    }

    if (
      rootAddress.includes(route.address) &&
      rootAddress.includes(routes[index + 1].address)
    ) {
      let nextPrice: number = 1;
      let nextAmount: number = routeTraded.amount * (1 - fee);

      if (route.address === BNB_BEP20.address) {
        nextAmount = nextAmount * bnbPrice;
      }

      if (routes[index + 1].address === BNB_BEP20.address) {
        nextPrice = bnbPrice;
        nextAmount = nextAmount / nextPrice;
      }

      routeTraded = {
        address: routes[index + 1].address,
        price: nextPrice,
        amount: nextAmount,
      };

      return;
    }

    if (rootAddress.includes(route.address)) {
      const nextPrice: number = tokenOutput.price as number;
      let nextAmount: number = routeTraded.amount * (1 - fee);
      const reserve: number = Number(tokenOutput.targetPool?.reserve?.[0] || 0);
      const impact: number = nextAmount / (reserve + nextAmount);

      nextAmount = nextAmount * (1 - impact);

      if (route.address === BNB_BEP20.address) {
        nextAmount = (nextAmount * bnbPrice) / nextPrice;
      } else {
        nextAmount = nextAmount / nextPrice;
      }

      routeTraded = {
        address: routes[index + 1].address,
        price: nextPrice,
        amount: nextAmount,
      };

      return;
    }

    if (rootAddress.includes(routes[index + 1].address)) {
      let nextPrice: number = 1;
      let nextAmount: number = routeTraded.amount * (1 - fee);
      const reserve: number = Number(tokenInput.targetPool?.reserve?.[1] || 0);
      const impact: number = nextAmount / (reserve + nextAmount);

      nextAmount = nextAmount * (1 - impact);

      if (routes[index + 1].address === BNB_BEP20.address) {
        nextPrice = bnbPrice;
        nextAmount = (nextAmount * (tokenInput.price as number)) / nextPrice;
      } else {
        nextAmount = nextAmount * (tokenInput.price as number);
      }

      routeTraded = {
        address: routes[index + 1].address,
        price: nextPrice,
        amount: nextAmount,
      };

      return;
    }
  });

  priceImpact =
    (amountTraded -
      amountForFee -
      (routeTraded.amount * (tokenOutput.price as number)) /
        (tokenInput.price as number)) /
    amountTraded;

  if (_.round(priceImpact, 2) === 0) {
    priceImpact = fee * (routeSwaps.length - 1);
  }

  return { priceImpact: Math.abs(priceImpact), amountOut: routeTraded.amount };
}

export async function getTokenBalanceOfWallet(
  tokenAddress: string,
  walletAddress: string,
  tokenDecimals: number = 18
): Promise<string> {
  let balanceBig: BigNumber = BigNumber.from(0);

  if (tokenAddress === BNB_BEP20.address) {
    try {
      balanceBig = await bscProvider.getBalance(walletAddress);
    } catch (error) {
      toast.error('Get balance failure!');
      console.log(error);
    }

    const balance: string = utils.formatUnits(balanceBig, tokenDecimals);

    return balance;
  }

  const tokenContract: Contract = getBep20Contract(tokenAddress);

  try {
    balanceBig = await tokenContract.balanceOf(walletAddress);
  } catch (error) {
    toast.error('Get balance failure!');
    console.log(error);
  }

  const balance: string = utils.formatUnits(balanceBig, tokenDecimals);

  return balance;
}

export async function checkTokenAllowance(
  address: string,
  decimals: number = 18
): Promise<boolean> {
  if (address === BNB_BEP20.address) {
    return true;
  }

  const tokenContract: Contract = getBep20Contract(address);
  let allowanceBig: BigNumber = BigNumber.from(0);

  try {
    allowanceBig = await tokenContract.allowance(address, BEP20_PROXY);
  } catch (error) {
    toast.error('Get allowance failure!');
    console.log(error);
  }

  const allowance: number = Number(utils.formatUnits(allowanceBig, decimals));

  if (allowance > 0) {
    return true;
  }

  return false;
}

const swapService = {
  getTokenPoolSwaps,
  getTokenSwaps,
  getRouteSwap,
  getPriceImpact,
  getTokenBalanceOfWallet,
  checkTokenAllowance,
};

export default swapService;
