import { PureComponent, ReactElement } from 'react';
import _ from 'lodash';
import {
  BUSD_BEP20,
  USDT_BEP20,
  USDC_BEP20,
  BNB_BEP20,
} from '../../../../config/tokenBep20Config';
import tokenHelper from '../../../../helper/tokenHelper';
import style from './InputCurrencyAmount.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  isDisable: boolean;
  isLoadingToken: boolean;
  isLoadingBalance: boolean;
  address: string;
  balance: string;
  price: number;
  liquidity: number;
  quoteSymbol: string;
  value: string;
  onChange: (v: string) => void;
}
interface IOwnState {}

class InputCurrencyAmount extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  static defaultProps = {
    isDisable: false,
    isLoadingToken: false,
    isLoadingBalance: false,
    onChange: () => {},
  };

  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {};

    this.onChange = this.onChange.bind(this);
    this.onMaxValue = this.onMaxValue.bind(this);
  }

  private onChange(e: { target: { value: string } }) {
    const { onChange } = this.props;
    const value: string = e.target.value;
    const re = /^[0-9]*[.,]?[0-9]*$/;

    if (value.trim() === '') {
      onChange('');
    } else if (re.test(value)) {
      onChange(value.replace(',', '.'));
    }
  }

  private onMaxValue() {
    const { balance, onChange } = this.props;

    balance && onChange(balance);
  }

  private checkShowPrice(): boolean {
    return ![
      BUSD_BEP20.address,
      USDT_BEP20.address,
      USDC_BEP20.address,
    ].includes(this.props.address);
  }

  private checkShowLiquidity(): boolean {
    return ![
      BUSD_BEP20.address,
      USDT_BEP20.address,
      USDC_BEP20.address,
      BNB_BEP20.address,
    ].includes(this.props.address);
  }

  private buildPoolInfo(): string {
    const { isLoadingToken, price, liquidity, quoteSymbol } = this.props;

    if (isLoadingToken) {
      return 'Loading...';
    }

    if (!this.checkShowPrice()) {
      return '';
    }

    if (!!price) {
      let poolInfo = `$${tokenHelper.roundCurrency(price)}`;

      if (this.checkShowLiquidity() && !!liquidity) {
        poolInfo += `  ${tokenHelper.convertToInternationalCurrencySystem(
          liquidity
        )} (${quoteSymbol})`;
      }

      return poolInfo;
    }

    if (_.isNil(liquidity)) {
      return 'Unknown!';
    }

    return '';
  }

  render(): ReactElement {
    const { isDisable, isLoadingToken, isLoadingBalance, balance, value } =
      this.props;

    return (
      <label className={style.wrap}>
        <input
          autoFocus
          inputMode='decimal'
          className={style.input}
          placeholder='0.0'
          disabled={isDisable}
          value={value}
          onChange={this.onChange}
        />
        <div className={style.info}>
          <span className='fk-swap-text-info'>{this.buildPoolInfo()}</span>
          {!isDisable && balance !== value && (
            <button
              disabled={
                isLoadingToken || isLoadingBalance || Number(balance || 0) === 0
              }
              className={style.max}
              onClick={this.onMaxValue}
            >
              MAX
            </button>
          )}
        </div>
      </label>
    );
  }
}

export default InputCurrencyAmount;
